/**
* Utils class hold the methods to execute various Math operations
*
*/
export default class Utils{
  static add(a,b){
    return a + b;
  }
  static sub(a,b){
    return a - b;
  }
  static mul(a,b){
    return a * b;
  }
  static div(a,b){
    return a / b;
  }
}



